<template>
    <div id="index">
        <header class="header">
            <div class="title">
                <div class="icon iconfont icon-monitorjiankong"></div>
                <div class="name">监控平台</div>
            </div>
            <div class="head-top">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item>{{$route.meta[0]}}</el-breadcrumb-item>
                    <el-breadcrumb-item v-if="$route.name=='Monitor'" class="select-wrapper">
                        <div class="select-box">
                            <div class="label">主机id</div>
                            <div class="input">
                                <el-select v-model="value" placeholder="请选择" size="small" class="custom-select" @change="selectChange">
                                    <el-option v-for="item in options" :key="item.ID" :label="item.label" :value="item.value"></el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-breadcrumb-item>
                </el-breadcrumb>
                <div class="head-top-line"></div>
            </div>
        </header>
        <div class="container">
            <div class="aside">
                <el-menu class="menu-left" style="min-height: 100%" background-color="#324057" text-color="#ffffff" active-text-color="#ffd04b" :default-active="activateIndex" router>
                    <el-menu-item index="/monitor"><i class="el-icon-odometer"></i>主机监控</el-menu-item>
                    <el-menu-item index="/alarm"><i class="el-icon-alarm-clock"></i>告警配置</el-menu-item>
                    <el-menu-item index="/agent"><i class="el-icon-setting"></i>agent管理</el-menu-item>
                </el-menu>
            </div>
            <div class="main">
                <keep-alive>
                    <router-view></router-view>
                </keep-alive>
            </div>
        </div>
    </div>
</template>

<script>
import { hostAll } from '../request/api'
export default {
    data() {
        return {
            activateIndex: this.$route.meta[1],
            options: [],
            value: ''
        };
    },
     mounted() {
        hostAll().then(res=>{
            if(res.data.CODE == 200){
                let data = res.data.DATA
                for(let i=0;i<data.length;i++){
                    this.options.push({
                        label:data[i].HostID,
                        value:data[i].HostID,
                        id:data[i].ID
                    })
                }
                this.$store.commit('getHost',data)
                this.value = this.options[0].value
                sessionStorage.setItem('HostID',this.value)
            }
        })
    },
    methods:{
        selectChange(id){
            this.$store.commit('selectChange',id)
        }
    }
};
</script>

<style lang="less" scoped>
#index {
    height: 100%;
    .header {
        display: flex;
        height: 50px;
        .title {
            flex: none;
            width: 160px;
            background-color: #324057;
            color: white;
            font-size: 18px;
            display: flex;

            .icon-monitorjiankong {
                font-size: 30px;
                margin-left: 10px;
                line-height: 50px;
            }
            .name {
                margin-top: 12px;
                margin-left: 10px;
            }
        }
        .head-top {
            flex: 1;
            display: flex;
            flex-direction: column;
            // align-content: center;
            justify-content: center;
            .el-breadcrumb {
                margin-top: 10px;
                margin-left: 20px;
                border-left: 6px solid rgb(60, 148, 216);
                padding: 0 10px;
                height: 30px;
                font-size: 16px;
                span {
                    margin-top: 5px;
                    margin-left: 8px;
                }
                .select-wrapper {
                    margin: 0;
                    .select-box {
                        display: flex;
                        height: 28px;

                        .label {
                            display: flex;
                            align-items: center;
                            // background-color: #d2d7dc;
                            // color: #409eff;
                            font-weight: 600;
                            font-size: 13px;
                            padding: 0 10px 0 0;
                        }
                        .input {
                            /deep/ .el-input__inner {
                                height: 28px;
                                line-height: 28px;
                            }
                            /deep/ .el-input__suffix {
                                top: 6px;
                            }
                            /deep/ .el-input__icon {
                                line-height: inherit;
                            }
                            /deep/ .el-input__suffix-inner {
                                display: inline-block;
                            }
                        }
                    }
                }
            }
            .head-top-line {
                height: 10px;
                width: 99%;
                margin: 0 auto;
                border-bottom: 1px solid #dcdee2;
            }
        }
    }
    .container {
        display: flex;
        // height: 100%;
        min-height: calc(100% - 50px);
        width: 100%;
        .aside {
            flex: none;
            min-height: 100%;
            // height: 100%;
            // position: absolute;
            // top: 50px;
            // bottom: 0px;
            // left: 0px;
            .menu-left {
                min-height: 100%;
                width: 160px;
                border: none;
                i {
                    font-size: 24px;
                }
            }
            .el-menu {
                .el-menu-item {
                    font-size: 16px;
                }
            }
        }
        .main {
            flex: 1;
            // background-color: #f6f6f6;
            // min-height: 100%;
            width: 100%;
            box-sizing: border-box;
            padding: 10px 20px;
            overflow-x: hidden;
            // overflow: auto;
        }
    }
}
</style>